import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory, useRouteMatch } from 'react-router-dom';
import { Request } from '../../../contracts/Request';
import { createTableColumns, exceptMany, handleError } from '../../../helpers';
import { Table } from '../../Shared/Table';
import toastr from 'toastr';
import dayjs from 'dayjs';
import state from '../../../state';
import { User } from '../../../contracts/User';
import { TrackRequest } from '../../TrackRequest';

export function List() {
	const [requests, setRequests] = useState<Array<Request>>([]);
	const [processing, setProcessing] = useState(false);
	const match = useRouteMatch();
	const history = useHistory();

	const path = (path: string) => `${match.path}${path}`;

	const fetchRequests = async () => {
		setProcessing(true);
		try {
			const { data } = await axios.get<Array<Request>>('/requests');
			setRequests(exceptMany(data, ['file', 'file_id', 'user_id', 'document_type_id']));
		} catch (error) {
			console.log(error.toJSON());
			toastr.error('Unable to fetch requests.');
		} finally {
			setProcessing(false);
		}
	};

	const deleteRequest = async (id: any) => {
		setProcessing(true);
		try {
			await axios.delete(`/requests?id=${id}`);
			toastr.success('Request deleted successfully.');
			await fetchRequests();
		} catch (error) {
			console.log(error.toJSON());
			handleError(error);
			setProcessing(false);
		}
	};

	const user = state.get<User>('user');

	const filterToRole = (requests: Array<Request>) => {
		switch (user.role) {
			case 'Admin':
				return requests;
			case 'Processing':
				return requests.filter((request) => request.status === 'Processing');
			case 'Cashier':
				return requests.filter((request) => request.status === 'Waiting Payment');
			case 'Evaluation':
				return requests.filter((request) => request.status === 'Paid');
			case 'Signing':
				return requests;
			case 'Releasing':
				return requests.filter((request) => request.status === 'Signed' || request.status === 'Releasing');
			case 'Applicant':
				return requests.filter((request) => request.user_id === user.id);
		}
	};

	const isAcknowledgable = (request: Request) => {
		switch (request.status) {
			case 'Received':
				return user.role === 'Admin';
			case 'Processing':
				return user.role === 'Processing';
			case 'Waiting Payment':
				return user.role === 'Cashier';
			case 'Paid':
				return user.role === 'Evaluation';
			case 'Evaluated':
				return user.role === 'Signing';
			case 'Signed':
				return user.role === 'Releasing';
			default:
				return false;
		}
	};

	const renderAcknowledged = (request: Request) => {
		if (isAcknowledgable(request)) {
			return request.acknowledged ? (
				<button className='btn btn-warning btn-sm disabled' disabled>
					Done
				</button>
			) : (
				<button
					className='btn btn-info btn-sm'
					onClick={async (e) => {
						e.preventDefault();
						e.currentTarget.setAttribute('disabled', 'true');
						e.currentTarget.classList.add('disabled');
						e.currentTarget.textContent = 'Acknowledging...';
						try {
							await axios.put(`/requests?id=${request.id}`, { acknowledged: true });
							toastr.info('Request acknowledged.', 'Notice');
							await fetchRequests();
						} catch (error) {
							console.log(error.toJSON());
							toastr.error('Unable to acknowledge request.');
						}
					}}>
					Acknowledge
				</button>
			);
		}
		return request.acknowledged ? <span className='badge badge-green'>Yes</span> : <span className='badge badge-red'>No</span>;
	};

	useEffect(() => {
		fetchRequests();
	}, []);

	return (
		<>
			<TrackRequest />
			<Table
				title='Requests'
				columns={createTableColumns(requests)}
				data={filterToRole(requests).map((request) => ({
					...request,
					created_at: dayjs(request.created_at).fromNow(),
					updated_at: dayjs(request.updated_at).fromNow(),
					user: request.user!.name,
					documentType: request.documentType!.name,
					approved: request.approved ? (
						<span className='badge badge-green'>Yes</span>
					) : (
						<span className='badge badge-red'>No</span>
					),
					expired: request.expired ? <span className='badge badge-red'>Yes</span> : <span className='badge badge-green'>No</span>,
					status: <span className='badge badge-danger'>{request.status}</span>,
					acknowledged: renderAcknowledged(request),
				}))}
				processing={processing}
				onAddClick={() => history.push(path('add'))}
				onViewClick={({ id }) => {
					history.push(path(`${id}`));
				}}
				onEditClick={({ id }) => {
					history.push(path(`${id}/edit`));
				}}
				onDeleteConfirm={({ id }) => {
					deleteRequest(id);
				}}
				onRefreshClick={fetchRequests}
				withAction={true}
				pagination={true}
				dontShowView={user.role === 'Applicant'}
				dontShowDelete={user.role === 'Applicant'}
				disableAddButton={user.role !== 'Applicant'}
			/>
		</>
	);
}
