import React from 'react';
import { CMS } from '../../contracts/CMS';

type Props = {
	cms: Array<CMS>;
};

export function AboutUs(props: Props) {
	const find = (key: string, defaultValue: string) => {
		const cms = props.cms.find((cms) => cms.key === key);
		if (cms) {
			return cms.value;
		}
		return defaultValue;
	};

	return (
		<div className='section section-dark text-center'>
			<div className='container'>
				<h2 className='title'>{find('about-us-title', "Let's talk about us")}</h2>
				<div className='row'>
					<div className='col-md-4'>
						<div className={`card card-profile card-plain`}>
							<div className={`card-avatar`}>
								<a href='#avatar'>
									<img src={find('about-us-profile-1-picture', '/assets/img/faces/clem-onojeghuo-3.jpg')} alt='...' />
								</a>
							</div>
							<div className='card-body'>
								<a href='#paper-kit'>
									<div className='author'>
										<h4 className='card-title'>{find('about-us-profile-1-name', 'Henry Ford')}</h4>
										<h6 className='card-category'>{find('about-us-profile-1-position', 'Product Manager')}</h6>
									</div>
								</a>
								<p className='card-description text-center'>
									{find(
										'about-us-profile-1-description',
										'Teamwork is so important that it is virtually impossible for you to reach the heights of your capabilities or make the money that you want without becoming very good at it.'
									)}
								</p>
							</div>
						</div>
					</div>
					<div className='col-md-4'>
						<div className={`card card-profile card-plain`}>
							<div className={`card-avatar`}>
								<a href='#avatar'>
									<img src={find('about-us-profile-2-picture', '/assets/img/faces/joe-gardner-2.jpg')} alt='...' />
								</a>
							</div>
							<div className='card-body'>
								<a href='#paper-kit'>
									<div className='author'>
										<h4 className='card-title'>{find('about-us-profile-2-name', 'Sophie West')}</h4>
										<h6 className='card-category'>{find('about-us-profile-2-position', 'Designer')}</h6>
									</div>
								</a>
								<p className='card-description text-center'>
									{find(
										'about-us-profile-2-description',
										'A group becomes a team when each member is sure enough of himself and his contribution to praise the skill of the others. No one can whistle a symphony. It takes an orchestra to play it.'
									)}
								</p>
							</div>
						</div>
					</div>
					<div className='col-md-4'>
						<div className={`card card-profile card-plain`}>
							<div className={`card-avatar`}>
								<a href='#avatar'>
									<img src={find('about-us-profile-3-picture', '/assets/img/faces/erik-lucatero-2.jpg')} alt='...' />
								</a>
							</div>
							<div className='card-body'>
								<a href='#paper-kit'>
									<div className='author'>
										<h4 className='card-title'>{find('about-us-profile-3-name', 'Robert Orben')}</h4>
										<h6 className='card-category'>{find('about-us-profile-3-position', 'Developer')}</h6>
									</div>
								</a>
								<p className='card-description text-center'>
									{find(
										'about-us-profile-3-description',
										'The strength of the team is each individual member. The strength of each member is the team. If you can laugh together, you can work together, silence isnt golden, its deadly.'
									)}
								</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
