import React from 'react';
import { Link } from 'react-router-dom';
import { User } from '../../contracts/User';
import { routes } from '../../routes';
import state from '../../state';

export function Nav() {
	const user = state.get<User>('user');

	const logged = () => {
		return state.get('logged') && state.get('user');
	};

	return (
		<nav className='navbar navbar-expand-lg fixed-top navbar-transparent border-0' color-on-scroll='300'>
			<div className='container'>
				<div className='navbar-translate'>
					<a className='navbar-brand' href='/' rel='tooltip' title='PBBDTM' data-placement='bottom' target='_blank'>
						PBBDTM
					</a>
					<button
						className='navbar-toggler navbar-toggler'
						type='button'
						data-toggle='collapse'
						data-target='#navigation'
						aria-controls='navigation-index'
						aria-expanded='false'
						aria-label='Toggle navigation'>
						<span className='navbar-toggler-bar bar1'></span>
						<span className='navbar-toggler-bar bar2'></span>
						<span className='navbar-toggler-bar bar3'></span>
					</button>
				</div>
				<div className='collapse navbar-collapse justify-content-end' id='navigation'>
					<ul className='navbar-nav'>
						{!logged() ? (
							<>
								<li className='nav-item'>
									<Link to={routes.LOGIN} className='nav-link'>
										<i className='nc-icon nc-layout-11'></i>
										Login
									</Link>
								</li>
								<li className='nav-item'>
									<Link to={routes.REGISTER} className='nav-link'>
										<i className='nc-icon nc-book-bookmark'></i>
										Register
									</Link>
								</li>
							</>
						) : (
							<li className='nav-item'>
								<Link to={routes.DASHBOARD} className='nav-link'>
									<i className='nc-icon nc-bank'></i>
									Dashboard
								</Link>
							</li>
						)}
					</ul>
				</div>
			</div>
		</nav>
	);
}
