import React from 'react';
import { Link } from 'react-router-dom';
import { CMS } from '../../contracts/CMS';
import { routes } from '../../routes';

type Props = {
	cms: Array<CMS>;
};

export function Header(props: Props) {
	const find = (key: string, defaultValue: string) => {
		const cms = props.cms.find((cms) => cms.key === key);
		if (cms) {
			return cms.value;
		}
		return defaultValue;
	};

	return (
		<div
			className='page-header'
			style={{
				backgroundImage: 'url(/assets/img/daniel-olahh.jpg)',
				backgroundPosition: '50%',
				backgroundSize: 'cover',
				minHeight: '100vh',
				maxHeight: '999px',
				overflow: 'hidden',
				position: 'relative',
				width: '100%',
				zIndex: 1,
				display: 'flex',
				alignItems: 'center',
				justifyContent: 'center',
			}}>
			<div className='filter'></div>
			<div className='container'>
				<div className='text-center' style={{ color: '#fff', zIndex: 3, position: 'relative' }}>
					<h1>{find('header-title', 'Welcome to PBBDTM')}</h1>
					<h3>{find('header-description', 'PUP Bansud Branch Document Tracking and Monitoring')}</h3>
					<br />
					<Link to={routes.REGISTER} className={`btn btn-outline-neutral btn-round`}>
						<i className='fa fa-play'></i> Get Started
					</Link>
				</div>
			</div>
		</div>
	);
}
